import React from 'react'

import TaskPlot from './components/TaskPlot'
import CombinedResourcePlot from './components/CombinedResourcePlot'

import { Tooltip } from '@mui/material'
import { PrimaryButton, SecondaryButton } from 'src/styled'

import { OptimizationContainer, OptimizationCharts } from './styled'

import InfoIcon from '@mui/icons-material/Info'

const calculatePercentage = (optimized, baseline) => {
  const diff = (optimized - baseline)
  const percentage = ((diff / baseline) * 100)
  const checkPositive = Math.sign(percentage)
  if (checkPositive === 1) {
    return <span className='badge minus'>{(Math.round(percentage))}%</span>
  }
  else if (checkPositive === -1) {
    return <span className='badge plus'>{(Math.abs(Math.round(percentage)))}%</span>
  }
  else { return <span className='badge'>{(Math.round(percentage))}%</span> }
}

export default function OptimizedSummary({ selectedPreset, optimizer, goPrev, goNext }) {

  return <OptimizationContainer>
    <h2><span style={{ textTransform: 'capitalize' }}>{selectedPreset}</span> Optimized Summary </h2>
    <div className='summary'>
      <div className='stats'>
        <div className='title'>Project Duration (days)
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(optimizer?.results?.at(-1)?.drtn_day)} </div>
          <div className='baseline-stats'>{Math.round(optimizer?.baseline_result?.drtn_day)}</div>
        </div>
        {calculatePercentage(optimizer?.results?.at(-1)?.drtn_day, optimizer?.baseline_result?.drtn_day)}
      </div>
      <div className='stats'>
        <div className='title'>Resources
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(optimizer?.results?.at(-1)?.rsrc_max)} </div>
          <div className='baseline-stats'>{Math.round(optimizer?.baseline_result?.rsrc_max)} </div>
        </div>
        {calculatePercentage(optimizer?.results?.at(-1)?.rsrc_max, optimizer?.baseline_result?.rsrc_max)}
      </div>
      <div className='stats'>
        <div className='title'>Tasks <Tooltip title='Tasks that were changed'><InfoIcon /></Tooltip>
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(optimizer?.results?.at(-1)?.num_changed)} </div>
          <div className='baseline-stats'>{Math.round(Object.values(optimizer?.baseline_result?.inp?.tasks).length)} </div>
        </div>
        {calculatePercentage(Math.round(Object.values(optimizer?.baseline_result?.inp?.tasks).length) - optimizer?.results?.at(-1)?.num_changed, Object.values(optimizer?.baseline_result?.inp?.tasks).length)}
      </div>
      <div className='stats'>
        <div className='title'>Tasks on Critical Path
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(optimizer?.results[optimizer?.results?.length - 1]?.num_crt)} </div>
          <div className='baseline-stats'>{Math.round(optimizer?.baseline_result?.num_crt)}  </div>
        </div>
        {calculatePercentage(optimizer?.results.at(-1)?.num_crt, optimizer?.baseline_result?.num_crt)}
      </div>
    </div>
    {optimizer?.results?.length > 0 ?
      <OptimizationCharts>
        <div className='chart'>
          <h3>Tasks (Overview)</h3>
          <span className="subtitle">Overview of all tasks over time</span>
          <div className='legend'>
            <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
              <div style={{ width: '14px', height: '14px', background: '#82B5C7', marginRight: '5px', borderRadius: '50%' }}></div>
              <div>Optimized</div>
            </div>
            <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
              <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
              <div>Baseline</div>
            </div>
            <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
              <div style={{ width: '14px', height: '14px', background: '#e61717', marginRight: '5px', borderRadius: '50%' }}></div>
              <div>Critical Path</div>
            </div>
          </div>
          {optimizer.results[optimizer.results.length - 1]?.inp?.tasks ?
            <TaskPlot
              height="600"
              tasks={optimizer.results?.at(-1)?.inp.tasks}
              baselineTasks={optimizer.baseline_result?.inp.tasks}
              title='Tasks (Overview)' />
            : 'No data'
          }
        </div>
        <div className='chart'>
          <div className='row'>
            <div>
              <h3>Combined Resources</h3>
              <span className="subtitle">Total utilization of multiple resources over time</span>
            </div>
          </div>
          {optimizer.results?.at(-1)?.rsrc_dist ?
            <CombinedResourcePlot
              optimizer={optimizer}
              title='Combined Resources' />
            : 'No data'
          }
        </div>
      </OptimizationCharts> : ''}
    <div className='row'>
      <SecondaryButton variant='contained' onClick={goPrev}>
        Back
      </SecondaryButton>
      <PrimaryButton variant='contained' onClick={goNext}>
        View Detailed Report
      </PrimaryButton>
    </div>
  </OptimizationContainer>
}