import { toast } from 'react-toastify'

export class Optimizer {
    constructor(reader) {
      this.reader = reader
      this.sim_in = {
        steps: '100',
        default_start: '2023-01-01',
        min_ratio: '0.5',
        max_ratio: '1.5',
        lr: '0.005',
      }
      this.loss_weights = {
        drtn_day: 100,
        rsrc_max: 0,
        rsrc_std: 0,
        loss_task: 0,
        loss_rsrc: 0,
        loss_task_rsrc: 0,
      }
      this.results = []
    }
  
    init() {
      return new Promise((res) => {
        let url = 'wss://engine.frontline-optimizer.com'
        //let url = 'ws://127.0.0.1:8000'
        // console.log(url)
        this.ws = new WebSocket(url)
        this.ws.onopen = (e) => {
          console.log('Optimization Engine connected!')
          toast.success('Optimization Engine connected!')
          res()
        }
        this.ws.onclose = (e) => {
          console.log('Optimization Engine disconnected!')
          toast.error('Optimization Engine disconnected!')
          this.finishedCallback(this.results)
        }
  
        this.ws.onmessage = (e) => {
          let out = JSON.parse(e.data.replace(/\bNaN\b/g, 'null'))
          if (out.success) toast.success('Engine: ' + out.success)
          if (out.error) toast.error('Engine: ' + out.error)
          if (out.drtn_day) {
            this.modify_output(out)
            this.results.push(out)
            this.results = [
              this.results[0],
              ...this.results.slice(1).sort((a, b) => b.loss_sum - a.loss_sum),
            ]
          }
          this.resultCallback(this.results)
        }
        this.ws.onerror = (e) => {
          toast.error('Optimization Engine connection error!')
        }
      })
    }
  
    modify_output(out) {
      let num_changed = 0
      let task_rsrcs = {},
        task_preds = {}
      Object.values(out.inp.tasks).forEach((t) => {
        t.drtn_hr_2 = Math.round(t.drtn_hr_2)
        t.changed = t.drtn_hr !== t.drtn_hr_2
        t.drtn_ratio = t.drtn_hr_2 / t.drtn_hr
        if (t.changed) num_changed += 1
        Object.values(t.rsrcs).forEach((x) => {
          task_rsrcs[x.id] = x
        })
        Object.values(t.preds).forEach((x) => {
          task_preds[x.id] = x
        })
      })
      //console.log(task_rsrc, task_pred)
      Object.assign(out.inp, { task_preds, task_rsrcs })
      out.num_changed = num_changed
      out.num_crt = out.crt_path.length
      //this.debug_output(out)
      Object.values(out.inp.rsrcs).forEach((r) => {
        r.show_in_plot = true
      })
    }
  
    debug_output(out) {
      let { tasks, rsrcs, clndrs, task_preds, task_rsrcs } = out.inp
      if (this.results.length === 1) {
        console.log(out)
        for (let g of [tasks, rsrcs, clndrs, task_preds, task_rsrcs]) {
          console.log(Object.values(g)[0])
        }
      }
    }
  
    send(obj) {
      this.ws.send(JSON.stringify(obj))
    }
  
    async stop() {
      if(this.ws)
        this.ws.close()
    }
  
    async optimize(resultCallback, finishedCallback) {
      this.resultCallback = resultCallback
      this.finishedCallback = finishedCallback
      if (!this.ws)
        await this.init()
      else if (this.ws.readyState !== this.ws.OPEN) 
        await this.init()
      this.results = []
      let { tasks, rsrcs, clndrs } = this.reader.data
      if (this.reader?.cons_data) {
        let { tasks_cons, rsrcs_cons, task_rsrc_cons } = this.reader.cons_data
        tasks_cons.forEach((c) => {
            let t = tasks[c.id]
            t.drtn_hr_rng = [c.min_drtn_hr, c.max_drtn_hr]
        })
        rsrcs_cons.forEach((c) => {
            let r = rsrcs[c.id]
            r.rng = [0, c.max_units_per_day]
        })
        task_rsrc_cons.forEach((c) => {
            let x = tasks[c.t_id].rsrcs[c.r_id]
            x.units_rng = [c.min_units, c.max_units]
        })
      }
      this.send({
        func: 'optimize',
        tasks,
        rsrcs,
        clndrs,
        drtn_ratios: {},
        default_start: Date.parse(this.sim_in.default_start + 'Z'),
        steps: parseInt(this.sim_in.steps),
        ratio_rng: [
          parseFloat(this.sim_in.min_ratio),
          parseFloat(this.sim_in.max_ratio),
        ],
        lr: parseFloat(this.sim_in.lr),
        loss_weights: this.loss_weights
      })
    }
  }