import React, { useState } from 'react'

import Box from '@mui/material/Box'
// import FileDownloadIcon from '@mui/icons-material/FileDownload'

import { TableContent } from './styled'
import { LoadingModal, OptimizationContainer } from '../InputData/styled'
import { PrimaryButton, SecondaryButton } from 'src/styled'

import RawData from './RawData'
import ChangedTasksTable from './components/ChangedTasksTable'
import ChangedResourcesTable from './components/ChangedResourcesTable'
import ChangedResourcesTasksTable from './components/ChangedResourcesTaskTable '

export default function DetailedReport({ selectedPreset, optimizer, goPrev }) {
  const [openModal, setOpenModal] = useState(false)
  const [selectedResultIndex, setSelectedResultIndex] = useState(optimizer?.results?.length - 1)

  return (<>
    <h2><span style={{ textTransform: 'capitalize' }}>{selectedPreset}</span> Detailed Report</h2>
    <OptimizationContainer>
      <TableContent>
        <div className='row'>
          <h3>Changed Tasks (Overview)</h3>
          {/* <div><FileDownloadIcon /> Export</div> */}
        </div>
        <div className='legend' style={{ width: '400px', justifyContent: 'flex-start', marginBottom: '20px' }}>
          <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center', marginRight: '10px' }}>
            <div className='circle critical' /> Critical
          </div>
          <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
            <div className='circle' /> Non-Critical
          </div>
        </div>
        {optimizer?.results ?
          <ChangedTasksTable ariaLabel='changed-tasks-overview' results={optimizer?.results} /> : 'No tasks were changed!'}
      </TableContent>

      <h2>Resources Tables</h2>
      <TableContent>
        <h4>Changed Resources</h4>
        <ChangedResourcesTable ariaLabel='changed-resources' results={optimizer?.results} />
      </TableContent>

      <TableContent>
        <h4>Changed Task-Resources</h4>
        <ChangedResourcesTasksTable ariaLabel='changed-tasks-resources' results={optimizer?.results} />
      </TableContent>

      {optimizer?.loss_weights ? <RawData results={optimizer?.results} loss={optimizer?.loss_weights} selectedResultIndex={selectedResultIndex} handleChooseResult={(index) => {
        setSelectedResultIndex(index)
      }} /> : 'No data'}

      <div className='row'>
        <SecondaryButton variant='contained' onClick={goPrev}>
          Back
        </SecondaryButton>
        <PrimaryButton onClick={() => setOpenModal(true)} variant='contained'>
          Export Report
        </PrimaryButton>
      </div>
    </OptimizationContainer>

    <LoadingModal
      open={openModal}
      onClose={() => setOpenModal(false)}
      aria-labelledby='modal-modal-title'
      aria-describedby='modal-modal-description'
    >
      <Box sx={{
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        p: 3
      }}>
        <div className='title' id='modal-modal-title'>
          Export Optimized File
        </div>
        <div className='row'>
          <PrimaryButton onClick={() => {
            console.log(optimizer)
            optimizer.reader.merge_data(optimizer.results[selectedResultIndex].inp)
            optimizer.reader.export()
          }} variant='contained'>Original Format</PrimaryButton> &nbsp;
          <PrimaryButton onClick={() => {
            optimizer.reader.export_excel(optimizer.results[selectedResultIndex].inp)
          }} variant='contained'>Excel</PrimaryButton>
        </div>
      </Box>
    </LoadingModal>
  </>
  )
}