import React, { useState, useEffect, useRef, useReducer } from 'react'
import * as d3 from 'd3'
import { ToggleButton } from '@mui/material'

import { ToggleGroup } from '../styled'

import { init_d3 } from '../../../../utils/plot'
import { plot_color_palette } from '../../../../utils/color'
import { ms_per_day, len, down_sample } from '../../../../mods/a_utils'

export default function CombinedResourcePlot({
  optimizer,
  width = 560,
  height = 500,
  keep = 1000,
}) {
  const ref = useRef()
  const baseline_result = optimizer.baseline_result
  const optimized_result = optimizer.results.at(-1)
  const combined_dist = optimized_result.rsrc_dist
  let resources = optimized_result.inp.rsrcs

  const [showResourcePlot, setShowResourcePlot] = useState('optimized')
  const [_, forceUpdate] = useReducer((x) => x + 1, 0)

  useEffect(() => {
    if (!combined_dist) return

    if (showResourcePlot === 'optimized')
      resources = optimized_result.inp.rsrcs
    else if (showResourcePlot === 'baseline')
      resources = baseline_result.inp.rsrcs

    const data = down_sample(Object.entries(combined_dist), keep)
    const color = d3.scaleOrdinal(d3.schemeCategory10)

    const x_optimized_domain = d3.extent(Object.keys(combined_dist))
    const x_baseline_domain = d3.extent(Object.keys(baseline_result?.rsrc_dist))
    const x_domain = [
      Math.min(x_optimized_domain[0], x_baseline_domain[0]),
      Math.max(x_optimized_domain[1], x_baseline_domain[1]),
    ]

    const y_values = Object.values(resources).map((r) => {
      return Object.values(r.dist)
    })
    const y_domain = d3.extent(y_values.flat())

    const { svg, x_scale, y_scale } = init_d3({
      x_domain,
      y_domain,
      width,
      height,
      ref,
      // x_label: 'time',
      // y_label: 'units',
      x_type: d3.scaleUtc,
      y_type: d3.scaleLinear,
    })

    const colors = ['#f7dc5b98', '#82B5C798', '#939ce398', '#C2876D98', '#AABB5D98', '#db793098', '#099A4D98', '#f07e9398']

    let x = 0
    for (const r in resources) {
      const rect = svg.append('g')

      rect
        .selectAll('rect')
        .data(Object.entries(resources[r].dist))
        .join('rect')
        .attr('x', (d) => x_scale(d[0]))
        .attr('width', (d) => x_scale(ms_per_day) - x_scale(0))
        .attr('y', (d) => y_scale(d[1]))
        .attr('height', (d) => y_scale(y_domain[0]) - y_scale(d[1]))
        .attr('fill', (d, i) => {
          if (resources[r].show_in_plot && resources[r].type === 'Labor')
            return plot_color_palette.ResourcePlot.allResources[x % plot_color_palette.ResourcePlot.allResources.length]
          else
            return 'none'
        })
      x++
    }

  }, [combined_dist, showResourcePlot, _])

  function toggleSelectResource(r) {
    baseline_result.inp.rsrcs[r].show_in_plot = !baseline_result.inp.rsrcs[r].show_in_plot
    optimized_result.inp.rsrcs[r].show_in_plot = !optimized_result.inp.rsrcs[r].show_in_plot
    forceUpdate()
  }

  if (!combined_dist) return null
  const msg =
    len(combined_dist) > keep
      ? `(showing ${keep}/${len(combined_dist)} only due to limited pixels)`
      : ''
  return (
    <>
      <ToggleGroup
        className='row'
        value={showResourcePlot}
        exclusive
        onChange={(e, value) => { setShowResourcePlot(value) }}
      >
        <ToggleButton value='baseline'>
          Baseline
        </ToggleButton>
        <ToggleButton value='optimized'>
          Optimized
        </ToggleButton>
      </ToggleGroup>

      <div className='legend'>
        {Object.values(resources).map((r, i) => {
          if (r.type === 'Labor')
            return <div key={r + ' ' + i} style={{ opacity: resources[r.id]?.show_in_plot ? '100%' : '50%', cursor: 'pointer', display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }} onClick={(e, i) => { toggleSelectResource(r.id) }}>
              <div style={{ width: '14px', height: '14px', background: plot_color_palette.ResourcePlot.allResources[i % plot_color_palette.ResourcePlot.allResources.length], marginRight: '5px', borderRadius: '50%' }}></div>
              <div>{r.name}</div>
            </div>
        })}
      </div>
      {/* <Title>{title}</Title> */}
      <>{msg}</>
      <svg ref={ref}></svg>
    </>
  )
}