import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3'

import { init_d3, domains_with_margin } from '../../../../utils/plot'

export default function DistibutionPlot({
  results = [],
  width = 550, height = 500,
  k, x_label, y_label, onClick
}) {
  const ref = useRef()

  useEffect(() => {
    if (!results?.length) return null

    const x_domain = d3.extent(results.map((d) => d.drtn_day))
    const y_domain = d3.extent(results.map((d) => d[k]))
    const domains = domains_with_margin(x_domain, y_domain, 0.15)

    const { svg, x_scale, y_scale } = init_d3({
      x_domain: domains.x_domain,
      y_domain: domains.y_domain,
      margin: 50,
      width, height, ref,
      x_label,
      y_label,
      x_type: d3.scaleLinear,
      y_type: d3.scaleLinear,
    })

    const circle = svg.append('g')
    circle.selectAll('circle').data(results).join('circle')
      .attr('cx', d => x_scale(d.drtn_day))
      .attr('cy', d => y_scale(d[k]))
      .attr('r', d => d.selected ? 12 : 6)
      .attr('fill', (d) => {
        if (d.option === 'baseline') return '#cccccc'
        return d.color
      })
      .on('click', (e, d) => {
        if (d.option === 'baseline') return
        onClick(d)
      })
      .style('cursor', d => d.option === 'baseline' ? 'auto' : 'pointer')
  }, [results])

  if (!results?.length) return null

  return (
    <svg ref={ref}>
    </svg>
  )
}